<script>
	let count = 0;

	function handleClick() {
	  count += 1;
	}
</script>

<style>
	button {
	  background: #ff3e00;
	  color: white;
	  border: none;
	  padding: 12px 16px;
	  border-radius: 5px;
	}
</style>

<button on:click={handleClick}>
  Clicked {count} {count === 1 ? 'vez' : 'veces'}
</button>